<template>
    <div class="showuser">
        <el-col>
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">
            <el-form-item label="" prop="userUserName">
                <el-input v-model="ruleForm.userUserName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="userPassword" show-password>
                <el-input v-model="ruleForm.userPassword"></el-input>
            </el-form-item>
            <el-form-item label="" prop="userTelphone">
                <el-input maxlength="11" v-model="ruleForm.userTelphone"></el-input>
            </el-form-item>
            <el-form-item label="" prop="userShopName">
                <el-input v-model="ruleForm.userShopName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="userAddress">
                <el-input v-model="ruleForm.userAddress"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-switch v-model="ruleForm.userStatus" active-text="" inactive-text="" active-value="1" inactive-value="0"></el-switch>
            </el-form-item>
            <el-form-item label="" prop="userComment">
                <el-input type="textarea" v-model="ruleForm.userComment" :autosize="{ minRows: 4, maxRows: 5}"></el-input>
            </el-form-item>
            </el-form>
        </el-col>
    </div>
</template>


<script>
  export default {
    name : "ShowUser",
    data() {
        return {
            ruleForm: {
                userId : '',
                userUserName : '',
                userPassword : '',
                userTelphone : '',
                userAddress : '',
                userShopName : '',
                userComment : '',
                userStatus : '1'
            },
            rules: {
                userUserName : [{
                        required: true,         // 
                        message: '', // 
                        trigger: 'blur'         // 
                    },
                    {
                        min: 2,                 // 
                        max: 10,                 // 
                        message: ' 2  10 ', 
                        trigger: 'change' 
                    }
                ],
                userPassword : [{
                        required: true,
                        message: '',
                        trigger: 'blur' 
                    }
                    ,
                    {
                        pattern: /^\w{3,16}$/,    // 
                        message: '3~16',
                        trigger: 'change' 
                    }
                ],
                userTelphone: [{
                        required: true,
                        pattern: /^1[34578]\d{9}$/, 
                        message: '', 
                        trigger: 'change' 
                    }
                ],
                userShopName: [{
                        required: true,
                        message: '',
                        trigger: 'blur' 
                    }
                ],
                userAddress:[{
                    required: true,
                    message: '',
                    trigger: 'blur' 
                }],
                userComment:[{
                    required: true,
                    message: '',
                    trigger: 'blur' 
                }]

            }
        };
    },
    methods: {
        refreshFormInfo (userData) {
            console.log("");
            console.log(userData.userId);
            this.ruleForm.userId = userData.userId;
            this.ruleForm.userUserName = userData.userUserName;
            this.ruleForm.userPassword = userData.userPassword;
            this.ruleForm.userTelphone = userData.userTelphone;
            this.ruleForm.userAddress = userData.userAddress;
            this.ruleForm.userShopName = userData.userShopName;
            this.ruleForm.userComment = userData.userComment;
            this.ruleForm.userStatus = userData.userStatus;
        },
        getForm(){
            console.log(this.$refs['ruleForm']);
            return this.$refs.ruleForm;
        },
        getFormData(){
            return this.ruleForm;
        }
    }
  }
</script>