import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        // 
        loginAdminId: window.sessionStorage.getItem("loginAdminId"),

        // cookie
        loginUser: window.sessionStorage.getItem("loginUser"),

        // localStorage

        openTab: [], // 
        activeIndex: '/admininterface/mainpage' // 
    },
    actions: {
        // mutations
        // 
        loginAdmin(context, args) {
            context.commit("login_admin", args)
        },
        logoutAdmin(context, args) {
            context.commit("logout_admin", args)
        },


        // 
        loginUser(context, args) {
            context.commit("login_user", args)
        },
        logoutUser(context, args) {
            context.commit("logout_user", args)
        }
    },
    mutations: {
        // 
        login_admin(state, id) {
            console.log("admin login...");
            state.loginAdminId = id;
            window.sessionStorage.setItem("loginAdminId", id);
        },
        logout_admin(state) {
            console.log("admin logout...");
            state.loginAdminId = "";
            window.sessionStorage.setItem("loginAdminId", "");
        },


        // 
        login_user(state, user) {
            console.log("user login...");
            state.loginUserId = user;
            window.sessionStorage.setItem("loginUser", user);
        },
        logout_user(state) {
            console.log("user logout...");
            state.loginUser = null;
            window.sessionStorage.setItem("loginUser", null);
        },



        // tabs
        add_tabs(state, data) {
            this.state.openTab.push(data);
        },
        // tab
        set_active_index(state, index) {
            this.state.activeIndex = index;
        },
        // tabs
        delete_tabs(state, route) {
            let index = 0; // index,openTab
            for (let option of state.openTab) {
                if (option.route == route) {
                    break;
                }
                index++;
            }
            // indexn
            this.state.openTab.splice(index, 1);
        }
    }

})