<template slot-scope="xxx">
    <div class="queryorder">

    <!--  -->

      <div>
        <el-row>
            <el-button type="primary" style="width:110px;float:left;margin:10px" @click="exportExcel()" ></el-button>
            <el-button type="primary" style="width:60px;float:left;margin:10px" @click="queryAll()" ></el-button>

            <div class="fright" style="margin:10px">
                <el-input ref="input" v-model="str" placeholder="id" style="width:200px "></el-input>
                <el-input ref="input" v-model="shopName" placeholder="" style="width:200px "></el-input>
                <el-button type="primary" style="width:100px" @click="queryAll()" ></el-button>
            </div>
        </el-row>
      </div>
      

      <!--  table  -->
      <div style="odertable">
        <el-table :data="tableData" id="table" style="width: 100%; margin:10px" >
          <el-table-column  type="expand"  >
              <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand">
                  <div style="float:left"> 
                  <el-form-item label="id:">
                    {{props.row.orderId}}
                  </el-form-item><br>
                  <el-form-item label="id:">
                    {{props.row.orderUserId}}
                  </el-form-item><br>
                  <el-form-item label=":">
                    {{props.row.orderIsSend}}
                  </el-form-item><br>
                  <el-form-item label=":">
                    {{props.row.orderIsValid}}
                  </el-form-item><br>
                  <el-form-item label=":">
                    {{props.row.orderTotalPrice}}
                  </el-form-item><br>
                  <el-form-item label=":" >
                    {{props.row.orderStatus}}
                  </el-form-item><br>
                  <el-form-item label=":">
                    {{props.row.orderComment}}
                  </el-form-item><br>
                  </div>
                  <div style="float:right">
                  <el-form-item label=":" >
                    {{props.row.user.userUserName}}
                  </el-form-item><br>
                  <el-form-item label=" :">
                    {{props.row.user.userShopName}}
                  </el-form-item><br>
                  <el-form-item label=" :" >
                    {{props.row.user.userAddress }}
                  </el-form-item><br>
                  <el-form-item label=":">
                    {{props.row.user.userComment}}
                  </el-form-item><br>
                  <el-form-item label=":">
                    {{props.row.user.userStatus}}
                  </el-form-item><br>
                  <el-form-item label=" ID:">
                    {{props.row.orderUserId}}
                  </el-form-item><br>
                  </div>
                </el-form>
            </template>
          </el-table-column>


              <!--  -->
                
          <el-table-column prop="orderId" label="id" title="orderId"  width="180"> </el-table-column>
          <el-table-column prop="orderDateTime" label="" width="180"> </el-table-column>
          <el-table-column prop="orderTotalPrice" label="" width="180"> </el-table-column>
          <el-table-column prop="user.userShopName" label="" width="180"> </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <span >
                  <el-button type="mini"  @click="queryByid(scope.row.orderId)" v-bind:title="scope.row.orderId" ></el-button>
                  <el-button style="width:60px; margin-right:10px;" type="mini" @click="send(scope.row,scope.$index)" v-bind:title="scope.row.orderId" v-if="scope.row.orderIsSend=='0'" v-text="btntext" v-show="show"></el-button>
                  <el-button style="width:60px; margin-right:10px;" type="mini" @click="sendcom(scope.row.orderId,scope.$index) " v-bind:title="scope.row.orderId" v-if="scope.row.orderIsSend=='1 '"  v-text="btntext1"></el-button>
              </span>
            <el-dialog title=""  :visible.sync="dialogTableVisible">

                <!--  -->

                <el-table :data="list">
                  <el-table-column property="product.productName" label="" width="150"></el-table-column>
                  <el-table-column property="detailDateTime" label="" width="200"></el-table-column>
                  <el-table-column property="detailProductCount" label=""></el-table-column>
                  <el-table-column property="detailProductPrice" label=""></el-table-column>
                    <el-table-column property="detailTotalPrice" label=""></el-table-column>
                    <el-table-column property="detailProductPrice" label="">
                      <template slot-scope="scope">
                        <img  v-bind:src=src+scope.row.product.productPhoto alt="" style="width: 50px;height: 50px">
                      </template>
                    </el-table-column>
                    <!--  <el-table-column property="detailUserId" label=""></el-table-column> -->
                </el-table>
            </el-dialog>
                      <!--  ------------------------------------->
              <el-button size="mini" type="danger" @click="handleDelete(scope.row.orderId,scope.$index)"></el-button>
            </template>
          </el-table-column>
        </el-table>

        <!--  -->
        <el-row>
            <el-col :span="24">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page.sync="currentPage1"
                  :page-sizes="[10, 20, 30, 40]"
                  :page-size="size"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="length">
                </el-pagination>
            </el-col>
        </el-row>
      </div>


  </div>
</template>


<script>
import FileSaver from 'file-saver'
import XLSX from 'xlsx'
export default {
   name:'products',
      data(){
         return {
           BtnName:'',
           name:[],
           var:'',
           str:'',
           size:"10",
           shopName:'',
           list:'',
           list2:"",
           src:"http://www.wxhwg.com:8080/mock/static/",
           web_static_server:'http://www.wxhwg.com:8080/mock/static/',     
           length:'',
           currentPage1: 1,
          // total: 1,
           page: 1,
           btntext:"",
           btntext1:"",
           show:true,
           pageNum:1,
           mingxiid:'',
           yema:{
             currentPage:1,
             pageSize1:10,
            },
           
           /*  */
           tableData: [{
                orderId: '',
                orderDateTime: '',
                orderTotalPrice: '',
                orderStatus: '',
                orderIsValid: 1,
                orderComment: null,
                user: {
                userId: "",
                userUserName: "",
                userPassword: "",
                userTelphone: "",
                userAddress: "",
                userShopName: "",
                userComment: "",
                userStatus: ''
                }
             }],
           dialogTableVisible: false,
        }
      },
      props:{
            category:String,  //
            title:String
      },
     
      created(){
        this.queryAll()
      },
      methods:{

       /* ---------------------------------------- */

       handleDelete(str,index){
          this.$confirm(', ?', '',{
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
          }).then(() => {
             this.$axios.get("http://www.wxhwg.com:8080/mock/order/invalid",{
                    params:{
                      orderid:str, 
                    }
              }).then(response=>{ 
                let  result =response.data;
                  if(result.status=="200"){           
                      this.$message({
                       type: 'success',
                       message: '!', 
                      });
                      this.tableData.splice(index,1);
                  }
              }).catch(error=>{ 
                })
          }).catch(() => {
           this.$message({
             type: 'info',
             message: ''
            });       
          });       
      },



       /* ------------------------------------------ */
       
       queryAll(){

          this.$axios.get("http://www.wxhwg.com:8080/mock/order/list",{
                    params:{
                        pageNum:'1', 
                        pageSize:'1000',
                        orderId:this.str, 
                        shopName:this.shopName,
                        /* shopName:this.shopName,  */
                    }
          }).then(response=>{
                 
            let  result =response.data;
            if(result.status=="200"){
              this.length=result.data.list.length;
              this.tableData=result.data.list;
              this.tableData=this.tableData.splice(0,10);
              
            }
          }).catch(error=>{})
                
        }, 

       // -------------------
       send(id,index){
         this.$confirm('?', '', {
           confirmButtonText: '',
           cancelButtonText: '',
           type: 'warning'
          }).then(() => {
           this.$axios.get("http://www.wxhwg.com:8080/mock/order/send",{
                params:{
                  orderid:id.orderId, 
                }
            }).then(response=>{
                let  result =response.data;
                if(result.status=="200"){
                     
                    this.$message({
                      type: 'success',
                      message: '!',
                     
                    });
                    id.orderIsSend=1
                   
                  }else{
                    this.$message({
                      type: 'success',
                      message: '!'
                    });
                  }
            }).catch(error=>{})
        
          }).catch(() => {
            this.$message({
             type: 'info',
             message: ''
            });          
         });
        },
        sendcom(){
          this.$alert('', '', {
            confirmButtonText: '',
            callback: action => {}
          });
        },

        /* <!-- ------------------------------------------------ --> */

        handleSizeChange(val) {
        // console.log(` ${val} `);
        
        this.yema.pageSize1 = val;
         this.$axios.get("http://www.wxhwg.com:8080/mock/order/list",{
                    params:{
                        pageNum: this.yema.currentPage, 
                        pageSize:val,  
                    }
            }).then(response=>{
                        let  result =response.data;
                        if(result.status=="200"){
                          this.tableData=result.data.list;
                          let orderIsSend=result.data.list.orderIsSend
                          if(this.tableData.length!=val){
                            var num =val-this.tableData.length;
                            for(var index=0;index<num;index++){}
                          }
                        }else{
                           alert(result.message);
                         }
              }).catch(error=>{})
        },
        handleCurrentChange(val) {
          this.yema.currentPage = val;
        
          this.$axios.get("http://www.wxhwg.com:8080/mock/order/list",{
                    params:{
                        pageNum:val, 
                        pageSize: this.yema.pageSize1,  
                    }
            }).then(response=>{
                        let  result =response.data;
                        if(result.status=="200"){
                          this.tableData=result.data.list;
                          let orderIsSend=result.data.list.orderIsSend
                          if(this.tableData.length!=10){
                            var num =10-this.tableData.length;
                            for(var index=0;index<num;index++){}
                          }
                        }else{
                           alert(result.message);
                         }
              }).catch(error=>{})
        },
        getDetails(row){
         this.mingxiid=row.orderId
        },

    /* id----------------------------------- */
        queryByid(id){
          this.dialogTableVisible = true
          this.$axios.get("http://www.wxhwg.com:8080/mock/order/detail",{
                    params:{
                      orderid:id
                    }
            }).then(response=>{
                let  result =response.data;
                if(result.status=="200"){
                 this.list=result.data;  
                }
              }).catch(error=>{})
       },
       /*  -------------------------*/
       exportExcel(){
           /* generate workbook object from table */
            let wb = XLSX.utils.table_to_book(document.getElementById('table'));
            /* get binary string as output */
            let wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' });
            try {
              FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), '.xlsx');
            } catch (e){
              if (typeof console !== 'undefined')
              console.log(e, wbout)
            }
            return wbout
        }
    
     }
 }
</script>



<style scoped>

    .el-input{
      margin-right: 10px;
    }
</style>
